<template>
  <div style="padding:20px 16px;" v-cloak>
    <el-row class="card" v-if="subitem.subitems.length<=0">
      <el-col :span="8" class="label" style="text-align:right">{{subitem.name}}</el-col>
      <el-col :span="16" class="value" style="text-align:left">
        <input class="input" type="number" v-model="subitem.value" v-if="subitem.value_range==null">
        <div class="input" v-else @click="subitem.isPicker=true" v-html="subitem.value"></div>
        <myPicker :item.sync="subitem" v-if="subitem.value_range!=null"></myPicker>
      </el-col>
    </el-row>
    <el-row class="card" v-else v-for="item in subitem.subitems" v-cloak>
      <el-col :span="8" class="label" style="text-align:right">{{item.name}}</el-col>
      <el-col :span="16" class="value" style="text-align:left">
        <input class="input" type="number" v-model="item.value" v-if="item.sub_value_range==null">
        <div class="input"  v-else @click="openPopup(item.id)" v-html="item.value"></div>
        <myPicker :item.sync="item" v-if="item.sub_value_range!=null"></myPicker>
      </el-col>
    </el-row>


  <!-- <el-input type="textarea" disabled="disabled"></el-input> -->
  <mt-button class="submit" @click.native="submit">保存</mt-button>
  </div>


</template>
<script>
import { Indicator } from 'mint-ui';
import myPicker from "./picker.vue";
import { Toast } from 'mint-ui';
export default {
  components:{myPicker},
  data() {
    return {
      subitem:{
        subitems:[]
      },
    };
  },
  methods: {
    openPopup (id){
      this.subitem.subitems.map(item=>{
        if(item.id==id){
          setTimeout(()=>{
            item.isPicker =true;
          },300)
        }else{
          item.isPicker =false;
        }
      })
    },
    checkForm (){
      if(this.subitem.subitems.length<=0){
        if(this.subitem.value==""){
          Toast({
            message: '请填写'+this.subitem.name,
            position: 'middle',
            duration: 2000
          });
          return false
        }else {
          return true
        }

      }else{
        var flag = true
        for(var i=0;i<this.subitem.subitems.length;i++){
          if(this.subitem.subitems[i].value==""){
            Toast({
              message: '请填写'+this.subitem.subitems[i].name,
              position: 'middle',
              duration: 2000
            });
            flag =  false
            break;
          }

        }
        if(flag){
          return true
        }else{
          return false
        }
      }
    },
    getForm () {
      var list = [];
      if(this.subitem.subitems.length<=0){
        list.push(this.subitem.value);
      }else{
        this.subitem.subitems.map(item=>{
          list.push(item.value);
        })
      }
      return {
        health_item_id:this.subitem.id,
        content:list.join(",")|| ""
      }
    },
    submit (){

      if(!this.checkForm()){
        return false
      }
      Indicator.open();
      var resource = this.$resource(this.$store.state.serverIP +"/health_items/add_item_record");
      resource.save(this.getForm()).then((callback) => {
          Indicator.close();
        Toast({
          message: '保存成功',
          position: 'middle',
          duration: 2000
        });
        setTimeout(()=>{
          this.$router.push({path:"/index"});
        },2000)

      });
    },
    getArray (min,max){
      var arr=[];
      for (var i =min;i<=max;i++){

        arr.push(i)
      }
      return arr
    }

  },
  mounted () {

      var resource = this.$resource(this.$store.state.serverIP +"/health_items/health_item?health_item_id="+this.$route.params.id+"&"+location.search.substring(1).split("&")[0]);
      resource.get().then((callback) => {
        var item = callback.data;

        if(item.subitems.length<=0){
          item.value="";
          item.isPicker=false;
          if(item.value_range==null){
            this.subitem = item;
            Indicator.close();
            return false;
          }else{
            var  list = item.value_range.split(",");
            item.slots=[{
              flex: 1,
              values:this.getArray(Number(list[0]),Number(list[1])),
              className: '',
              textAlign: 'center'
            }]
          }

        }else{
          item.subitems.map(nav=>{
            var list =[];
            nav.value="";
            nav.isPicker=false;
            if(nav.sub_value_range==null){
              this.subitem = item;
              Indicator.close();
              return false;
            }else{
              list = nav.sub_value_range.split(",");
              nav.slots=[{
                flex: 1,
                values:this.getArray(Number(list[0]),Number(list[1])),
                className: 'slot1',
                textAlign: 'center'
              }]
            }

          })

        }
        this.subitem = item;
        Indicator.close();
      },error=>{
        Indicator.close();
      });




  }

}
</script>

<style   lang="scss">
.card{
  text-align: center;
  overflow: hidden;
  margin: 0px auto;
  margin-bottom: 30px;
  .label,.value {
    height: 70px;
    color: #323232;
    line-height: 70px;
  }
  .value .input{
      text-align: center;
      border: 1px solid #ccc;
      width: 120px;
      font-weight: 100;
      color: #b01421;
      font-size: 50px;
      height: 66px;
      line-height: 66px;
      margin-left: 10px;
      border-radius: 4px;
      outline: none;
  }
}

.submit{
	background-color: #A21A28;
	outline: none;
	color: #fff;
	width: 100%;
  margin: 20px auto;
	font-family: '微软雅黑';
}
</style>



